import React from 'react'
import classNames from 'classnames'

export default function Tag({ as, className, children, ...restProps }) {
  const tagClassname = classNames([
    'mr-2 px-2 py-[.15rem] rounded bg-gray-200 text-xs',
    className,
  ])

  const El = as || 'span'
  return (
    <El className={tagClassname} {...restProps}>
      {children}
    </El>
  )
}
